//Import fonts from https://fonts.google.com/
@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap');
// Font Variables
$font-primary: "Manrope", sans-serif ;
$base-font-size: 1rem;
$extBold-font-weight: 800;
$bold-font-weight: 700;
$semiBold-font-weight: 600;
$medium-font-weight: 500;
$normal-font-weight: 400;
$light-font-weight: 300;
$header-line-height: 1.4;
$paragraph-line-height:1.8;
$base-letter-spacing:-0.02em;
$zero-letter-spacing: 0;

//Colors 
$color-body: #E5E5E5;
$color-white : #fff;
$color-black: #000;
$color-light:  #C7E6F4;
$color-lighter: #F9FDFF;
$color-primary: #1C4456;
$color-primary-light: #417086;
$color-primary-lighter: #5D8A9E;
$color-primary-lightest: #96BCCE;
$color-danger: #FF5555;
$color-warning: #FFF8F8;
$color-blue: #00ADF7;

$color-header: #1C4456;
$color-text: #417086;

// Spacer Variables
$base-padding: 1rem;
$base-margin: 1rem;

// Transition Variables
$base-transition: all 0.3s ease-in-out;

//Borders
$base-border-width: 1px;
$base-border-radius: 10px;
$button-border-radius: 10px;

//Shadows
$large-box-shadow: 0px 30px 60px rgba(19, 52, 68, 0.2);
$medium-box-shadow: 0px 30px 30px rgba(146, 174, 187, 0.2);
$base-box-shadow: 0px 9px 20px rgba(28, 68, 86, 0.2);